import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { ContactsProvider } from '../../providers/contacts/contacts';
import Format from 'string-format';

@IonicPage()
@Component({
  selector: 'page-collection',
  templateUrl: 'collection.html',
})
export class CollectionPage {

  pre: any;

  accounts: any = [];
  unmasked_accounts: any = [];
  objectblock: any = {
    username: '', account_from: '', lang: '', account_to: '', biller_ref: '',
    ministry_name: '',refref:'', amount: '', 
  };
  cname: string = "";
  amount: string = "";
  error: number = 0;
  ispay: boolean = false;
  avail: boolean = false;
  unpaid: boolean = false;
  langs: any = {};
  allNavdata: any = {};
  title: string = "";
  constructor( public contactsProvider: ContactsProvider,public navCtrl: NavController, public globalVars: GlobalVarsProvider, public modalCtrl: ModalController,
    public alertService: AlertServiceProvider, private clientdata: ClientdataProvider, public navParams: NavParams) {

    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[ 0 ].acc_number;
    this.allNavdata = navParams.get( 'data' );
    this.title = this.allNavdata.title;
    this.objectblock.biller_ref = this.allNavdata.name;
    if ( this.title == 'Union payment' )
    { this.avail = true; } 

    //    if ( this.title == 'Zanzibar Other Institutions' )
    // { 
    //     console.log( "............go direct......");
    // // this.gotonext();
    //  this.navCtrl.push( 'GovernmentPage2');
    
    //  } 
    

  
  }

 

  //  gotonext() {
  //        console.log( "............humu2shouldgo......",this.objectblock.biller_ref );
  //       //  this.navCtrl.setRoot('HomePage');
  //    this.navCtrl.push( 'FundstransferPage');
  // }

  //     ionViewDidLoad() {
  //   console.log('ionViewDidLoad CollectionPage');
  // }

 
  goHome() {
    this.navCtrl.setRoot('HomePage');
  }

  back() {
 if(this.ispay)  {
 this.ispay = false;
   this.objectblock.account_to = "";
 }else
    this.navCtrl.pop();
  }
  pickContact() {
    this.objectblock.accountto = "";
    this.contactsProvider.pickContact().then( contact => {
      if ( contact.phoneNo.length > 1 ) {
        if ( contact.phoneNo[ 0 ] == "" || contact.phoneNo[ 0 ] == undefined ) {
          this.alertService.mytoastdown( this.langs.valid_topup_number );
        } else {
          this.objectblock.account_to = contact.phoneNo[ 0 ];
        } }
      else {
        if ( contact.phoneNo[ 0 ] == "" || contact.phoneNo[ 0 ] == undefined ) {
          this.alertService.mytoastdown( this.langs.valid_topup_number );
        } else {
          this.objectblock.account_to = contact.phoneNo[ 0 ];
        }
      }
    } );
  }
  enquire() {
    if (this.objectblock.account_to === null || this.objectblock.account_to === '' || this.objectblock.account_to === undefined) {
       this.error = 1;
    } else {
      this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.collectionspre( this.objectblock, this.allNavdata.url )
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if (data.length != 0) {
            this.alertService.dismiss();
            if (!data.error) {
              this.pre = data;

        
              this.ispay = true;
              this.objectblock.amount = this.pre.amount;
              this.objectblock.refref= this.pre.referenceNo;

              if ( this.pre.status == '2' ) {
                this.unpaid = false;
              } else { 
                this.unpaid = true;
              }
            } else if (data.error) {
              this.alertService.errorPop(data.field48);
            } else {
              this.alertService.errorPop(this.langs.tech);
            }
          } else {
            this.alertService.myLoadingDismiss();
            this.alertService.errorPop( this.langs.tech );
          }
        }, err => {
          console.log( err );
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
        });
    }
  }
  submit() {
    if (this.objectblock.account_from === null || this.objectblock.account_from === '') {
      this.alertService.errorPop(this.langs.invalid_acc);
    } else {
     
      console.log(".................ll", this.pre)
    
      this.objectblock.account_to = this.pre.account_number;
      this.objectblock.ministry_code = this.pre.ministrycode;
      this.objectblock.ministry_name = this.pre.controlnumber;
      this.objectblock.refref = this.pre.refenceNo;


      if (this.objectblock.refref === null || this.objectblock.refref === '') {
        this.objectblock.refref= "00";
      }

      let message = Format( this.langs.collection_meso, this.objectblock,this.pre );
      let template = "<div>" + message + "</div>";
      let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
      let myModal = this.modalCtrl.create('ConfirmModalPage', obj);
      myModal.present();
      myModal.onDidDismiss(data => {
        if (data) {
          this.request();
        }
        else {  }
      });
    }
  }

  ionViewDidEnter() {
    this.globalVars.reload();

  }
  request() {
    this.alertService.showLoading( this.langs.submitting_txt );
    this.clientdata.paygovernement(this.objectblock)
  
      .subscribe( data => {
        data = this.globalVars.testdec( data );
        console.log( data )
        if ( data.length != 0 ) {
          this.alertService.dismiss();
          if ( !data.error ) {
            this.alertService.errorPop( data.message );
            this.navCtrl.pop();
          } else if ( data.error ) {
            this.alertService.errorPop( data.message );
            this.navCtrl.pop();
          } else {
            this.alertService.errorPop( this.langs.tech );
            this.navCtrl.pop();
          }
        } else {
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
          this.navCtrl.pop();
        }
      }, err => {
        console.log( err );
        this.alertService.dismiss();
        this.alertService.errorPop( this.langs.tech );
      } );
  }

}




